module.exports = "/**\n *\n * 你好啊。我叫卫正明。目前就职于安徽阡陌科技，高级前端工程师。\n *\n * 我喜欢捣鼓一些新奇的玩意儿。\n *\n * 要不来一场直播写代码？\n */\n\n/**\n * 那么开始吧，首先让一切都动起来~\n */\n\n* {\n  -webkit-transition: all 1s;\n}\n\n/**\n * 这看似没用呀，别急，一会儿你就会看到效果了。\n *\n * 黑白配色太无聊了，我们来做一些变化。\n */\n\nhtml {\n  background: rgb(63, 82, 99);\n}\n\n/***\n * 等等。\n */\n\npre, a {\n  color: white;\n}\n\n/**\n * 看上去好多了。但是有点刺眼了。\n *\n * 而且在整个屏幕上写代码有点浪费空间了。\n *\n * 不要急哦，让我来做一些优化。\n */\n\npre:not(:empty) {\n  overflow: auto;\n  background: rgb(48, 48, 48);\n  border: 1px solid #ccc;\n  max-height: 44.6%;\n  width: 49%;\n  font-size: 14px;\n  font-family: monospace;\n  padding: 10px 10px 20px;\n  box-shadow: -4px 4px 2px 0 rgba(0,0,0,0.3);\n  white-space: pre-wrap;\n  outline: 0;\n  position: absolute;\n}\n\n/**\n * 接下来我们在把它移到屏幕右侧。\n */\n\n#style-text {\n  -webkit-transform: translateX(95%);\n  position: absolute;\n}\n\n/**\n * 看上去还不错喔, 但是所有的字体颜色是白色的！\n *\n * 那么，我们来把代码变的更加可读吧~\n */\n\n.comment       { color: #857F6B; font-style: italic; }\n.selector      { color: #E69F0F; }\n.selector .key { color: #64D5EA; }\n.key           { color: #64D5EA; }\n.value         { color: #BE84F2; }\n.value.px      { color: #F92772; }\n\n/**\n * 现在我们有了一些进展。\n *\n * 再把它变得更立体一些。\n */\n\nbody {\n  -webkit-perspective: 1000px;\n}\n\n#style-text {\n  -webkit-transform: translateX(98.5%) rotateY(-10deg);\n  -webkit-transform-origin: right;\n  max-height: 94.5%;\n}\n\n/**\n * 好吧差不多了，接下来就聊聊我吧-(一个有多正经就有多深情的男人)。\n *\n * 我相信你应该来是看我简历的吧？\n */\n\npre:not(#style-text) {\n  -webkit-transform: rotateY(10deg);\n  -webkit-transform-origin: left;\n}\n/**/\n\n"